"use client";
import React, { useState } from "react";
import { FaPaperPlane } from "react-icons/fa";
import { FiMoreHorizontal } from "react-icons/fi";
import { mockUsers, mockMessages } from "../../../data/mockData";

function Sidebar({ users, selectedUserId, onSelect }) {
  return (
    <aside className=" border-r border-[#23272e] flex flex-col">
      <div className="p-4">
        <input
          type="text"
          placeholder="Search"
          className="w-full px-4 py-2 rounded-lg bg-[#23272e] text-white placeholder-gray-400 focus:outline-none"
        />
      </div>
      <div className="flex-1 overflow-y-auto">
        <div className="px-4 text-gray-400 text-xs mb-2">Direct Messages</div>
        {users.map((user) => (
          <div
            key={user.id}
            className={`flex items-center gap-3 px-4 py-3 rounded-lg cursor-pointer mb-1 transition ${
              selectedUserId === user.id
                ? "bg-[#23272e] text-white"
                : "hover:bg-[#23272e] text-gray-300"
            }`}
            onClick={() => onSelect(user.id)}
          >
            <img
              src={user.avatar}
              alt={user.name}
              className="w-12 h-12 rounded-full object-cover"
            />
            <span className="flex-1 truncate text-lg">{user.name}</span>
          </div>
        ))}
      </div>
    </aside>
  );
}

function ChatHeader({ user }) {
  return (
    <div className="flex items-center px-8 py-8 border-b border-[#23272e]">
      <img
        src={user.avatar}
        alt={user.name}
        className="w-14 h-14 rounded-full object-cover mr-6"
      />
      <div>
        <div className="font-semibold text-2xl">{user.name}</div>
      </div>
      <div className="ml-auto">
        <FiMoreHorizontal className="text-3xl text-gray-400 cursor-pointer" />
      </div>
    </div>
  );
}

function ChatMessages({ messages }) {
  return (
    <div className="flex-1 overflow-y-auto px-8 py-8 space-y-8">
      {messages.map((msg) => (
        <div
          key={msg.id}
          className={`flex ${msg.mine ? "justify-end" : "justify-start"}`}
        >
          {!msg.mine && (
            <img
              src={msg.avatar}
              alt={msg.sender}
              className="w-10 h-10 rounded-full object-cover mr-4 self-end"
            />
          )}
          <div>
            <div
              className={`${
                msg.mine
                  ? "bg-[#23272e] text-white rounded-xl rounded-br-none"
                  : "bg-[#23272e] text-white rounded-xl rounded-bl-none"
              } px-7 py-5 max-w-[90vw] md:max-w-2xl text-lg break-words`}
            >
              {msg.text}
            </div>
            <div
              className={`text-sm text-gray-400 mt-2 ${
                msg.mine ? "text-right" : "text-left"
              }`}
            >
              {msg.time}
            </div>
          </div>
        </div>
      ))}
    </div>
  );
}

function ChatInput({ value, onChange, onSend }) {
  return (
    <form
      className="flex items-center px-8 py-8 border-t border-[#23272e] bg-[#181b20]"
      onSubmit={(e) => {
        e.preventDefault();
        onSend();
      }}
    >
      <input
        type="text"
        placeholder="Type a message..."
        value={value}
        onChange={(e) => onChange(e.target.value)}
        className="flex-1 px-6 py-5 rounded-lg bg-[#23272e] text-white placeholder-gray-400 focus:outline-none text-lg"
      />
      <button
        type="submit"
        className="ml-6 px-8 py-5 bg-blue-600 hover:bg-blue-700 text-white rounded-lg flex items-center gap-3 text-lg"
      >
        <FaPaperPlane />
        Send
      </button>
    </form>
  );
}

export default function ChatPage() {
  const [selectedUserId, setSelectedUserId] = useState(mockUsers[0].id);
  const [input, setInput] = useState("");
  const [messages, setMessages] = useState(mockMessages);

  const selectedUser = mockUsers.find((u) => u.id === selectedUserId);

  const handleSend = () => {
    if (!input.trim()) return;
    setMessages([
      ...messages,
      {
        id: messages.length + 1,
        sender: "me",
        text: input,
        time: new Date().toLocaleTimeString([], { hour: "2-digit", minute: "2-digit" }),
        mine: true,
      },
    ]);
    setInput("");
  };

  return (
    <div className="fixed  flex flex-col h-screen w-screen">
      <div className="flex flex-1 min-h-0">
        <Sidebar
          users={mockUsers}
          selectedUserId={selectedUserId}
          onSelect={setSelectedUserId}
        />
        <section className="flex-1 flex flex-col">
          <ChatHeader user={selectedUser} />
          <ChatMessages messages={messages} />
          <ChatInput value={input} onChange={setInput} onSend={handleSend} />
        </section>
      </div>
      {/* Responsive: Sidebar collapses on small screens */}
      <style jsx global>{`
        @media (max-width: 768px) {
          aside {
            min-width: 0 !important;
            width: 64px !important;
          }
          aside input,
          aside .px-4.text-gray-400 {
            display: none !important;
          }
          aside img {
            margin: 0 auto !important;
          }
          aside span {
            display: none !important;
          }
        }
        @media (max-width: 640px) {
          .px-8 {
            padding-left: 1rem !important;
            padding-right: 1rem !important;
          }
          .py-8 {
            padding-top: 1rem !important;
            padding-bottom: 1rem !important;
          }
        }
      `}</style>
    </div>
  );
}
